@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
    html,body {
        height: 100%;
    }
    body {
        margin: 0;
        background: rgb(9,15,15);
        background: linear-gradient(10deg, #cdcccb, #589aad);
        animation: gradient 15s ease infinite;
        font-family: monospace;
        background-size: 400% 400%;
    }
    /* body {
        background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
        animation: gradient 15s ease infinite;

    } */
    
    @keyframes gradient {
        0% {
            background-position: 0% 50%;
        }
        50% {
            background-position: 100% 50%;
        }
        100% {
            background-position: 0% 50%;
        }
    }
    
    li {
        list-style: none;
    }
    a {
        text-decoration: none;
    }
    #root {
        height: 100%;
    }
}